.contact__container{
    display: grid;
    place-items: center;
}

.contact__wrapper{
    width: fit-content;
    display: flex;
    gap: 2rem;
}

.contact__wrapper a{
    margin-top: -5.5rem;
    background: var(--color-primary);
    font-size: 2rem;
    border: 2px solid transparent;
    transition: var(--transition);
    padding: 2rem;
    border-radius: 1.5rem; 
} 

.contact__wrapper a:hover{
    background: transparent;
    border-color: var(--color-gray-100);
}


/* MEDIA QUERIES (small screens) */

@media screen and (max-width: 600px) {
    .contact__wrapper{
        gap: 1.5rem;
    }
    .contact__wrapper a{
        font-size: 1.7rem;
        border-radius: 1rem;
    }
}
